<template>
	<div class="wrapper">
		<div class="form">
			<div class="form-item">
				<div class="label">userName</div>
				<at-input v-model="userInfo.userName"></at-input>
			</div>
			<div class="form-item">
				<div class="label">email</div>
				<at-input v-model="userInfo.email"></at-input>
			</div>
			<div class="form-item">
				<div class="label">city</div>
				<at-input v-model="userInfo.city"></at-input>
			</div>
			<div class="form-item">
				<div class="label">gender</div>
				<at-radio v-model="userInfo.gender" :label="0">Male</at-radio>
				<at-radio v-model="userInfo.gender" :label="1">Female</at-radio>
			</div>
			<div class="form-item">
				<div class="label">age</div>
				<at-input-number :min="0" :max="100" v-model="userInfo.age"></at-input-number>
			</div>
			<br />
			<at-button type="primary" @click="submit">Submit</at-button>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
			<at-button type="primary" @click="showModal = true">Reset Password</at-button>
			<br />
			<br />
			<h2>My Games</h2>

			<at-table :columns="gameCols" :data="gameList" :border="false"></at-table>
			<br />
			<br />
			<h2>My Friends</h2>

			<at-table :columns="friendCols" :data="friendList" :border="false"></at-table>
			<at-modal
				v-model="showModal"
				title="Reset Password"
				@on-confirm="handleConfirm"
				@on-cancel="handleCancel"
			>
				<div class="form">
					<div class="form-item">
						<div class="label">Old Password</div>
						<at-input type="password" v-model="resetInfo.oldPwd"></at-input>
					</div>
					<div class="form-item">
						<div class="label">New Password</div>
						<at-input type="password" v-model="resetInfo.newPwd"></at-input>
					</div>
				</div>
			</at-modal>
		</div>
	</div>
</template>

<script>
export default {
	created() {
		this.userInfo = JSON.parse(sessionStorage.getItem('userInfo')) || {}
		this.getMyGames()
		this.getMyFriends()
	},
	data() {
		return {
			userInfo: {},
			resetInfo: {},
            showModal: false,
            gameCols: [
                { title: 'Name', key: 'gameName' },
				{
					title: 'Action',
					render: (h, params) => {
						return h('AtButton', {
							props: {
								size: 'smaller',
								type: 'info',
								hollow: true
							},
							on: {
								click: () => {
									this.$http({
										url: '/game/delToUser',
										method: 'Delete',
										data: {
											gameId: params.item.id,
											userId: sessionStorage.getItem('userId')
										}
									}).then(res => {
										this.$Notify({
											title: 'Success',
											type: 'success'
										})
										this.getMyGames()
									})
								}
							}
						}, 'Delete')
					}
				}
            ],
            friendCols: [
                { title: 'userName', key: 'userName' },
				{
					title: 'Action',
					render: (h, params) => {
						return h('AtButton', {
							props: {
								size: 'smaller',
								type: 'info',
								hollow: true
							},
							on: {
								click: () => {
									this.$http({
										url: '/friendship/delete',
										method: 'delete',
										data: {
											friendId: params.item.id,
											userId: sessionStorage.getItem('userId')
										}
									}).then(res => {
										this.$Notify({
											title: 'Success',
											type: 'success'
										})
										this.getMyFriends()
									})
								}
							}
						}, 'Delete')
					}
				}
            ],
            gameList: [],
            friendList: []
		}
	},
	methods: {
		handleConfirm() {
			this.$http({
				url: '/user/resetPwd',
				method: 'put',
				data: {
					...this.resetInfo,
					uid: sessionStorage.getItem('userId')
				}
			}).then(res => {
				this.$Modal.alert({
					title: 'Success',
					content: 'Please login again',
					callback: function (action) {
						this.$router.push('/')
					}
				})

			})
		},
		handleCancel() {

		},
		getMyGames() {

			this.$http({
				url: '/game/viewUserGame',
				method: 'get',
				data: {
					userId: sessionStorage.getItem('userId'),
					pageSize: 9999,
					pageNo: 1
				}
			}).then(res => {
				this.gameList = res.data || []
			})
		},
		submit() {
			this.$http({
				url: '/user/updateUserInfo',
				method: 'put',
				data: {
					...this.userInfo,
					uid: sessionStorage.getItem('userId')
				}
			}).then(res => {
				this.$Notify({
					title: 'Success',
					type: 'success'
				})
				sessionStorage.setItem('userName', this.userInfo.userName)
				sessionStorage.setItem('userInfo', JSON.stringify(this.userInfo))
			})
		},
		getMyFriends() {
			this.$http({
				url: '/friendship/myFriends',
				method: 'get',
				data: {
					userId: sessionStorage.getItem('userId'),
					pageSize: 9999,
					pageNo: 1
				}
			}).then(res => {
				this.friendList = res.data || []
			})
		}
	}

}
</script>

<style lang="less" scoped>
.wrapper {
	display: flex;
	justify-content: center;
	padding-top: 50px;
}
.form {
	width: 500px;

	.label {
		font-size: 16px;
		font-weight: bold;
		margin-top: 20px;
	}
}
</style>