import Vue from 'vue'
import Router from 'vue-router'
import Login from './views/Login.vue'
import Register from './views/Register.vue'
import Home from './views/Home.vue'
import My from './views/My.vue'
import Friends from './views/Friends.vue'
import Club from './views/Club.vue'
import Page from './views/Page.vue'
import Game from './views/Game.vue'

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            name: 'login',
            component: Login
        },
        {
            path: '/register',
            name: 'register',
            component: Register
        },
        {
            path: '/page',
            name: 'page',
            component: Page,
            redirect: '/page/home',
            children: [
                {
                    path: '/page/home',
                    name: 'home',
                    component: Home
                },
                {
                    path: '/page/game',
                    name: 'game',
                    component: Game
                },
                {
                    path: '/page/my',
                    name: 'my',
                    component: My
                },
                {
                    path: '/page/friends',
                    name: 'friends',
                    component: Friends
                },
                {
                    path: '/page/club',
                    name: 'club',
                    component: Club
                }
            ]
        },
    ]
})
